import { AppBar, CssBaseline, Tab, Tabs, Toolbar,Box, Typography } from "@material-ui/core";
import { ShoppingCart } from "@material-ui/icons";
import {useMediaQuery,useTheme} from "@material-ui/core";
import React, { useState } from "react";
import {useStyle} from "./styles";
import Searchbar from "./Searchbar";
import Drawerc from "./Drawerc";
import FooterComp from "./FooterComp";
import CardComp from "./Cardcomp";
const App=()=>{
    const [val,setVal]=useState(0);
    const classes=useStyle();
    const theme=useTheme();
    const isMatch=useMediaQuery(theme.breakpoints.down("md"));
    return(
        <>
         {
            isMatch?(
                   <>
                      <CssBaseline/>
                        <AppBar position="fixed">
                         <Toolbar>
                            <ShoppingCart fontSize="large"/>
                            <Typography
                              variant="h6"
                              color="inherit" className={classes.shop}>
                                SHOPEE
                              </Typography>
                            {/* <Tabs textColor="inherit" value={val} TabIndicatorProps={{style:{background:"#e0c025"}}} onChange={(cur,index)=>setVal(index)}>
                                <Tab label="PRODUCTS"/>
                                <Tab label="SERVICES"/>
                                <Tab label="CONTACT US"/>
                                <Tab label="ABOUT US"/>
                                <Tab label="INFO"/>
                            </Tabs> */}
                            {/* <Box  sx={{marginLeft:"auto"}}>
                                <Avatar src={faizan} alt="Faizan" variant="circle" fontSize="large" className={classes.avat}/>
                            </Box> */}
                            <Box  sx={{marginLeft:"auto"}}>
                                <Searchbar className={classes.place}/>
                            </Box>
                            <Drawerc/>
                        </Toolbar>  
                       </AppBar>
                       <CardComp/>
                       <FooterComp/>
                   </>
                   ):(
                        <>
                            <CssBaseline/>
                                <AppBar position="fixed" color="secondary">
                                <Toolbar>
                                    <ShoppingCart fontSize="large"/>
                                    <Tabs textColor="inherit" value={val} TabIndicatorProps={{style:{background:"#e0c025"}}} onChange={(cur,index)=>setVal(index)}>
                                        <Tab label="PRODUCTS"/>
                                        <Tab label="SERVICES"/>
                                        <Tab label="CONTACT US"/>
                                        <Tab label="ABOUT US"/>
                                        <Tab label="INFO"/>
                                    </Tabs>
                                    {/* <Box  sx={{marginLeft:"auto"}}>
                                        <Avatar src={faizan} alt="Faizan" variant="circle" fontSize="large" className={classes.avat}/>
                                    </Box> */}
                                    <Box  sx={{marginLeft:"auto"}}>
                                        <Searchbar className={classes.place}/>
                                    </Box>
                                    {/* <Drawerc/> */}
                                </Toolbar>  
                            </AppBar>
                            <CardComp/>
                            <FooterComp/> 
                    </>     
                )
         }
                 
</>)}

export default App;