import * as React from 'react';
import {Card,CardActions,CardContent,CardMedia,Button,Typography,Container,Grid} from '@material-ui/core';
import {details} from './carddetails';
import { useStyle } from './styles';
const CardComp=()=> {
    const classes=useStyle();
  return (
    <>
        <Container className={classes.cardGrid} max-width="md">
            <Grid container spacing={4} className={classes.cen}>
              
                {
                    details.map((cur,index)=>{
                        return(
                          <>
                          <Grid item key={cur.id} style={{width:"300px"}}> 
                          <Card sx={{ maxWidth: 345 }}>
                          <CardMedia
                            component="img"
                            height="200px"
                            image={cur.src}
                            alt={cur.alt}
                          />
                          <CardContent>
                            <Typography gutterBottom variant="h5" component="div">
                              {cur.detail}
                            </Typography>
                            <Typography variant="body2" color="text.secondary">
                              Lizards are a widespread group of squamate reptiles, with over 6,000
                              species, ranging across all continents except Antarctica
                            </Typography>
                          </CardContent>
                          <CardActions>
                            <Button size="small">Share</Button>
                            <Button size="small">Learn More</Button>
                          </CardActions>
                        </Card>
                        </Grid>
                          </>)})
                }
             </Grid>
            </Container>        
    </>);
}
export default CardComp;