import React from "react";
import {Container,Grid,Box,Link} from "@material-ui/core"
// Now we will create our Footer using material ui
// Lets talk about how to craete footer
// <footer>-->represents it is a footer
// Box-->simply box-->wrapper component-->means we can put anything in box
// by default-->Box-->div
// u can change -->component="span" or other tag
// props-->bgcolor="red" width={100} height={100}
/*
  shorthand margin and padding props
  1)margin-->m
  2)padding-->p
  3)t,b,r,l-->top,bottom,right,left
  4)x and y-->horizontal direction and vertical direction
    syntax:
    {property}{side}:{value}
   5)mx->margin left and right
   6)my->margin top and bottom
   7)px-->padding for left and rigjt
   8)py->padding for top an bottom
    9)m-->margin for all sides
    10)p-->padding for all sides
    11)using theme.spacing->Another  way to space components in react js
      BY default value:8px
      spacing={top,bottom,right,left}
      we provide spacing factor
   12)We have provided padding in teh box
     that is for sm amd xs devices-->mobile phone and tablet
 */
const FooterComp=()=>{
  return(
    <>
     <footer>
        <Box bgcolor="lightgray" 
          px={{xs:3,sm:10}}
          py={{xs:5,sm:10}}
          sx={{background:"lightgray",fontWeight:"bold"}}
          style={{a:{
            color:"inherit"
          }}}
          spacing={3}
        >
           <Container maxWidth="lg">
                <Grid container spacing={5}>
                   <Grid item xs={12} sm={12} md={4} >
                      <Box borderBottom={1}>
                        Help
                      </Box>
                      <Box>
                        <Link href="/">Contact</Link>
                      </Box>
                      <Box>
                        <Link href="/">Support</Link>
                      </Box>
                      <Box>
                        <Link href="/">Privacy</Link>
                      </Box>
                   </Grid>
                   <Grid item xs={12} sm={12} md={4}>
                      <Box borderBottom={1}>
                        Help
                      </Box>
                      <Box>
                        <Link href="/">Contact</Link>
                      </Box>
                      <Box>
                        <Link href="/">Support</Link>
                      </Box>
                      <Box>
                        <Link href="/">Privacy</Link>
                      </Box>
                   </Grid>
                   <Grid item xs={12} sm={12} md={4}>
                      <Box borderBottom={1}>
                        Help
                      </Box>
                      <Box>
                        <Link href="/">Contact</Link>
                      </Box>
                      <Box>
                        <Link href="/">Support</Link>
                      </Box>
                      <Box>
                        <Link href="/">Privacy</Link>
                      </Box>
                   </Grid>
                </Grid>
                <Box textAlign="center" pt={{xs:5,sm:10}} pb={{xs:5,sm:0}} sx={{color:"gray"}} fontSize="20px">
                      SHOPPING SITE ©️ {new Date().getFullYear()}
                </Box>
           </Container>
         
        </Box>        
     </footer>
  </>)
}
export default FooterComp;

