import { Drawer, IconButton, List, ListItemIcon, ListItemText ,Avatar,Box} from "@material-ui/core";
import React, { useState } from "react";
import faizan from "./photos/faizan.jpg";
import MenuIcon from '@material-ui/icons/Menu';
import { useStyle } from "./styles";
// with the help of PaperProps u can change the width of drawer
const Drawerc=()=>{
    const classes=useStyle();
    const items=["PRODUCTS","SERVICES","ABOUT US","CONTACT US","INFO"]
    const [openDrawer,setOpenDrawer]=useState(false);
    return(
        <>
        
         <Drawer open={openDrawer}
          onClose={()=>setOpenDrawer(false)}
          PaperProps={{style:{width:"80vw"}}}
         >
            <Box sx={{display:"flex",alignItems:"center",justifyContent:"center",marginTop:"20px"}}>
                <Avatar src={faizan} alt="Faizan" variant="circle" fontSize="large"/>
            </Box>
            {
                items.map((cur,index)=>{
                    return(
                        <>
                        <List key={index} >
                            <IconButton onClick={()=>setOpenDrawer(false)}>
                                <ListItemIcon>
                                <ListItemText>
                                    {cur}
                                </ListItemText>
                                </ListItemIcon>
                            </IconButton>
                        </List>
                    </>)
                })
            }
           
         </Drawer>
         <IconButton onClick={()=>setOpenDrawer(!openDrawer)}>
            <MenuIcon fontSize="large" 
            className={classes.menu}/>
         </IconButton>
    </>)
}
export default Drawerc;