import { makeStyles } from "@material-ui/core";
export const useStyle=makeStyles((theme)=>({
    avat:{
       border: "3px dotted magenta",
    },
    menu:{
        color: "white",
    },
    shop:{
        marginLeft:"20px"
    },
    cardGrid:{
        padding: "20px 0",
        marginTop:"100px"
      },
      card:{
        height: "100%",
        display: "flex",
        flexDirection:"column"
      },
      cardMedia:{
        paddingTop:"10px",
        height: "33%",
        width:"33%",
        marginLeft:"33%"
      },
      cardContent:{
        flexGrow:"1"
      },
      cen:{
        justifyContent:"center"
      }
}))